import { Table } from 'rsuite';
const { Column, HeaderCell, Cell } = Table;


export const FraudCell = ({ rowData, dataKey, ...props }) => {
    return (
      <Cell {...props} style={{ padding: 0, alignItems: 'center' }}>
        <span className={`logs-item-value-fraud ${rowData[dataKey] ? 'fraud' : ''}`}></span>
      </Cell>
    );
  };