import React, { useState } from "react";
import { BrowserRouter as Router, Routes, Route } from "react-router-dom";
import Header from "./components/Header/Header";
import Login from "./components/Login/Login";
import Register from "./components/Register/Register";

import { LogsView } from "./components/Logs/view";
import { SingleLogView } from "./components/SingleLog/view";

import { useToaster } from "rsuite";

import "./App.css";
import 'rsuite/dist/rsuite.min.css';


const App = () => {
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const toaster = useToaster();

  const handleLogin = () => setIsAuthenticated(true); 
  const handleLogout = () => setIsAuthenticated(false); 

  return (
    <Router>
      <Header isAuthenticated={isAuthenticated} onLogout={handleLogout} toaster={toaster} />
      <Routes>
        <Route path="/" element={<h1>Добро пожаловать!</h1>} />
        <Route path="/login" element={<Login onLogin={handleLogin} />} />
        <Route path="/register" element={<Register onRegister={handleLogin} />} />
        <Route path="/stats" element={<h1>Главная статистика</h1>} />
        <Route path="/logs" element={<LogsView />} />
        <Route path="/log-check" element={<SingleLogView />} />
      </Routes>
    </Router>
  );
};

export default App;
