import { Input, Form, Panel } from "rsuite";
import { BasicView } from "../Main/basicView";

const KEY_NAMES = {
    "transaction_id": "ID",
    "ip": "IP",
    "device_id": "ID устройства",
    "device_type": "Тип устройства",
    "tran_code": "Код операции",
    "mcc": "МКК",
    "client_id": "ID клиента",
    "card_type": "Тип карты",
    "pin_inc_count": "Попытки ввода ПИН",
    "card_status": "Статус карты",
    "expiration_date": "Срок действия",
    "datetime": "Дата и время",
    "sum": "Сумма",
    "oper_type": "Тип операции",
    "balance": "Баланс",
    "is_fraud": "Фрауд"
}

export function SingleLogView () {
    return (
        <BasicView>
            <Panel header={"Проверка транзакции"} bordered className="log-form">
                <Form fluid>
                    {
                        Object.keys(KEY_NAMES).map((key) => (
                            <Form.Group controlId="log">
                                <Form.ControlLabel>{KEY_NAMES[key]}</Form.ControlLabel>
                                <Form.Control name={key} type="text" />
                            </Form.Group>
                        ))
                    }
                </Form>
            </Panel>
        </BasicView>
    )
}