import { Table } from 'rsuite';
import { FraudCell } from './fraudCell';

const { Column, HeaderCell, Cell } = Table;
const CompactCell = props => <Cell {...props} style={{ padding: 4 }} />;
const CompactHeaderCell = props => <HeaderCell {...props} style={{ padding: 4 }} />;

export default function LogsTable ({data}) {
    const compact = true;

    const CustomCell = compact ? CompactCell : Cell;
    const CustomHeaderCell = compact ? CompactHeaderCell : HeaderCell;

    return (
        <Table compa style={{ width: '100%'}}
        virtualized fillHeight data={data} loading={data.length === 0}
        headerHeight={compact ? 30 : 40}
        rowHeight={compact ? 30 : 46}>
            <Column align="center" flexGrow={1}>
                <CustomHeaderCell>Transaction ID</CustomHeaderCell>
                <CustomCell dataKey="transaction_id" />
            </Column>
            <Column align="center">
                <CustomHeaderCell>IP</CustomHeaderCell>
                <CustomCell dataKey="ip" />
            </Column>
            <Column align="center">
                <CustomHeaderCell>Device ID</CustomHeaderCell>
                <CustomCell dataKey="device_id" />
            </Column>
            <Column align="center">
                <CustomHeaderCell>Device Type</CustomHeaderCell>
                <CustomCell dataKey="device_type" />
            </Column>
            <Column align="center">
                <CustomHeaderCell>Transaction Code</CustomHeaderCell>
                <CustomCell dataKey="tran_code" />
            </Column>
            <Column align="center">
                <CustomHeaderCell>MCC</CustomHeaderCell>
                <CustomCell dataKey="mcc" />
            </Column>
            <Column align="center">
                <CustomHeaderCell>Client ID</CustomHeaderCell>
                <CustomCell dataKey="client_id" />
            </Column>
            <Column align="center">
                <CustomHeaderCell>Card Type</CustomHeaderCell>
                <CustomCell dataKey="card_type" />
            </Column>
            <Column align="center">
                <CustomHeaderCell>Pin Increment Count</CustomHeaderCell>
                <CustomCell dataKey="pin_inc_count" />
            </Column>
            <Column align="center">
                <CustomHeaderCell>Card Status</CustomHeaderCell>
                <CustomCell dataKey="card_status" />
            </Column>
            <Column align="center">
                <CustomHeaderCell>Expiration Date</CustomHeaderCell>
                <CustomCell dataKey="expiration_date" />
            </Column>
            <Column align="center">
                <CustomHeaderCell>Date Time</CustomHeaderCell>
                <CustomCell dataKey="datetime" />
            </Column>
            <Column align="center">
                <CustomHeaderCell>Sum</CustomHeaderCell>
                <CustomCell dataKey="sum" />
            </Column>
            <Column align="center">
                <CustomHeaderCell>Operation Type</CustomHeaderCell>
                <CustomCell dataKey="oper_type" />
            </Column>
            <Column align="center">
                <CustomHeaderCell>Balance</CustomHeaderCell>
                <CustomCell dataKey="balance" />
            </Column>
            <Column align="center" flexGrow={1}>
                <CustomHeaderCell>Fraud</CustomHeaderCell>
                <FraudCell dataKey="is_fraud" />
            </Column>
        </Table>
    )
}