import { useState, useEffect } from "react";
import { get_logs } from "../../api/get_logs";
import { IconButton } from "rsuite";
import ReloadIcon from '@rsuite/icons/Reload';
import { socket } from "../../socket_context/sockets";
import LogsTable from "./logsTable";

export default function LogsList () {
    const [logs, setLogs] = useState([]);

    const keys_grows = {
        "transaction_id": 2,
        "ip": 3,
        "device_id": 3,
        "device_type": 3,
        "tran_code": 2,
        "mcc": 1,
        "client_id": 3,
        "card_type": 2,
        "pin_inc_count": 2,
        "card_status": 2,
        "expiration_date": 3,
        "datetime": 4,
        "sum": 2,
        "oper_type": 3,
        "balance": 2,
        "is_fraud": 2
    }

    const keys_names = {
        "transaction_id": "ID",
        "ip": "IP",
        "device_id": "ID устройства",
        "device_type": "Тип устройства",
        "tran_code": "Код операции",
        "mcc": "МКК",
        "client_id": "ID клиента",
        "card_type": "Тип карты",
        "pin_inc_count": "Попытки ввода ПИН",
        "card_status": "Статус карты",
        "expiration_date": "Срок действия",
        "datetime": "Дата и время",
        "sum": "Сумма",
        "oper_type": "Тип операции",
        "balance": "Баланс",
        "is_fraud": "Фрауд"
    }

    useEffect(() => {
        get_logs().then((data) => setLogs(prev => {
            let new_data = [];

            for (let i = 0; i < 55; i++) {
                new_data.push(data[0]);
            }

            return new_data;
        }));
    }, []);

    useEffect(() => {
        socket.off("log");
        socket.on("log", (data) => {
            setLogs(prev => {
                let new_data = [data, ...prev];
                return new_data;
            });
        });
    }, []);

    useEffect(() => {
        socket.emit("subscribe_logs");
    }, []);

    return (
        <div className="logs-view-wrapper">
            <LogsTable data={logs} keys_grows={keys_grows} keys_names={keys_names} />
        </div>
    )
}